<template>
  <div class="add">
    <el-dialog :title="info.title" :visible.sync="info.show" @opened="opened">
      <el-form :model="form">
         <el-form-item label="一级分类" :label-width="formLabelWidth">
          <el-select v-model="form.first_cateid" @change="changeCate(false)">
            <el-option label="" value="" disabled>--请选择--</el-option>
            <!-- value值为变量 -->
            <el-option v-for="item in cateList" :key="item.id" :label="item.catename" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="二级分类" :label-width="formLabelWidth">
          <el-select v-model="form.second_cateid">
            <el-option label="" value="" disabled>--请选择--</el-option>
            <!-- value值为变量 -->
            <el-option v-for="item in secondCate" :key="item.id" :label="item.catename" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商品名称" :label-width="formLabelWidth">
          <el-input v-model="form.goodsname"></el-input>
        </el-form-item>
        <el-form-item label="商品价格" :label-width="formLabelWidth">
          <el-input v-model="form.price"></el-input>
        </el-form-item>
        <el-form-item label="市场价格" :label-width="formLabelWidth">
          <el-input v-model="form.market_price"></el-input>
        </el-form-item>
        <el-form-item label="图片" :label-width="formLabelWidth">
          <div class="box-img">
              <h3>+</h3>
              <img v-if="imageUrl" :src="imageUrl" >
              <input type="file" @change="changeImg1">
            </div>
        </el-form-item>
        <el-form-item label="商品规格" :label-width="formLabelWidth">
          <el-select v-model="form.specsid" @change="changeSpec(false)">
            <el-option label="" value="" disabled>--请选择--</el-option>
            <!-- value值为变量 -->
            <el-option v-for="item in specList" :key="item.id" :label="item.specsname" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="规格属性" :label-width="formLabelWidth">
          <el-select v-model="form.specsattr" multiple>
            <el-option label="" value="" disabled>--请选择--</el-option>
            <!-- value值为变量 -->
            <el-option v-for="item in secondSpec" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否新品" :label-width="formLabelWidth">
          <el-radio v-model="form.isnew" :label="1">是</el-radio>
          <el-radio v-model="form.isnew" :label="2">否</el-radio>
        </el-form-item>
         <el-form-item label="是否热卖" :label-width="formLabelWidth">
          <el-radio v-model="form.ishot" :label="1">是</el-radio>
          <el-radio v-model="form.ishot" :label="2">否</el-radio>
        </el-form-item>
        <el-form-item label="商品状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            active-color="blue"
            inactive-color="grey"
            :active-value="1"
            :inactive-value="2"
          >
          </el-switch>
        </el-form-item>
        <el-form-item label="商品描述" :label-width="formLabelWidth">
            <div id="div1"></div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="confirm" v-if="info.isAdd">确 定</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
import { warningAlert, successAlert } from '../../../utils/alert'
import E from 'wangeditor'
import { addGoods, oneGoods, updateGoods } from '../../../utils/request'
export default {
  props:['info'],
  data(){
    return {
      formLabelWidth:'120px',
      imageUrl:'',//要显示的图片地址
      secondCate:[],//二级分类列表
      secondSpec:[],//规格属性,
      editor:'',//富文本的内容
      form:{
        first_cateid:'',
        second_cateid:'',
        goodsname:'',
        price:'',
        market_price:'',
        img:'',
        description:'',
        specsid:'',
        specsattr:[],
        isnew:1,
        ishot:1,
        status:1
      }
    }
  },
  computed:{
    ...mapGetters({
      'cateList':"cate/cateList",
      "specList":'spec/specList',
    })
  },
  methods:{
    ...mapActions({
      "requestCateList":"cate/cateListActions",
      "requestSpecList":"spec/specListActions",
      "requestCount":"goods/countActions",
      "requestGoodsList":"goods/goodsListActions",
    }),
    cancel(){
      this.info.show = false;
      this.form = {
        first_cateid:'',
        second_cateid:'',
        goodsname:'',
        price:'',
        market_price:'',
        img:'',
        description:'',
        specsid:'',
        specsattr:[],
        isnew:1,
        ishot:1,
        status:1
      };
      this.imageUrl = ''
    },
    confirm(){
      this.form.description = this.editor.txt.html();
      addGoods(this.form).then(res=>{
        successAlert(res.data.msg);
        this.cancel()
        this.requestCount();
        this.requestGoodsList(null)
      })
    },
    opened(){
         // 创建富文本
        this.editor = new E('#div1')
        this.editor.create()
        this.editor.txt.html(this.form.description)
    },
    // 获取二级分类
    changeCate(bool){
      if(!bool){
        this.form.second_cateid = ''
      }
      // 获取一级分类改变时的index
      var index = this.cateList.findIndex(item=>item.id==this.form.first_cateid);
      // 获取二级分类
      this.secondCate = this.cateList[index].children;
    },
    // 获取规格属性
    changeSpec(bool){
      if(!bool){
        console.log(333);
        this.form.specsattr = [];
      }
      // 改变商品规格时获取规格属性
      var index = this.specList.findIndex(item=>item.id==this.form.specsid)
      this.secondSpec = this.specList[index].attrs;
    },
    changeImg1(e){
      var file = e.target.files[0];
      if(file.size > 2*1024*1024){
        warningAlert('图片大小不能超过2M')
        return
      }

      var ext = ['.png','.jpg','.jpeg','.gif']
      var extName = file.name.slice(file.name.lastIndexOf('.'))
      if(!ext.some(item=>item==extName)){
        warningAlert('上传文件格式不正确')
        return
      }

      this.imageUrl = URL.createObjectURL(file)
      this.form.img = file
    },
    getDetail(id){
        oneGoods({id}).then(res=>{
          this.form = res.data.list;
          this.form.id = id;
          this.imageUrl = this.imgUrl+this.form.img;
          // 获取二级分类
          this.changeCate(true);
          // 获取规格属性列表
          this.changeSpec(true);
          this.form.specsattr = this.form.specsattr.split(',')
        })
    },
    update(){
      this.form.description = this.editor.txt.html()
      updateGoods(this.form).then(res=>{
        successAlert(res.data.msg);
        this.cancel()
        this.requestGoodsList(null)
      })
    }

  },
  mounted(){
    // 获取一级分类数据
    this.requestCateList()
    // 获取商品规格
    this.requestSpecList();

  }
}
</script>

<style scoped>
 .box-img{
    width:150px;
    height:150px;
    border:1px dashed pink;
    position: relative;
  }
  .box-img h3{
    width:100%;
    height: 100%;
    line-height: 150px;
    text-align: center;
  }
  .box-img img{
    width:100%;
    height: 100%;
    position: absolute;
    left:0;
    top:0;
  }
  .box-img input{
    width: 100%;
    height: 100%;
    position: absolute;
    left:0;
    top:0;
    opacity: 0;
  }
</style>



