<template>
  <div class="list">
    <el-table
      :data="goodsList"
      style="width: 100%;margin-bottom: 20px;"
      row-key="id"
      border
      :tree-props="{ children: 'children'}"
    >
      <el-table-column prop="id" label="商品编号"  ></el-table-column>
      <el-table-column prop="goodsname" label="商品名称" ></el-table-column>
      <el-table-column prop="price" label="商品价格"> </el-table-column>
      <el-table-column prop="market_price" label="市场价格"> </el-table-column>
      <el-table-column  label="图片">
        <template v-slot="prop">
          <img class="img" :src="imgUrl+prop.row.img" alt="">
        </template>
      </el-table-column>
      <el-table-column prop="isnew" label="是否新品">
          <template v-slot="scope">
            <el-button type="primary" v-if="scope.row.isnew==1">是</el-button>
            <el-button type="danger" v-else>否</el-button>
          </template>
      </el-table-column>
      <el-table-column prop="ishot" label="是否热卖">
          <template v-slot="scope">
            <el-button type="primary" v-if="scope.row.ishot==1">是</el-button>
            <el-button type="danger" v-else>否</el-button>
          </template>
      </el-table-column>
      <el-table-column prop="status" label="状态">
          <template v-slot="scope">
            <el-button type="primary" v-if="scope.row.status==1">启用</el-button>
            <el-button type="danger" v-else>禁用</el-button>
          </template>
      </el-table-column>
      <el-table-column  label="操作">
          <template v-slot="scope">
            <el-button type="primary" @click="edit(scope.row.id)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.id)">删除</el-button>
          </template>
      </el-table-column>
    </el-table>
     <el-pagination
        v-if="value"
        background
        layout="prev, pager, next"
        :page-size="2"
        :total="count" @current-change="cPage">
      </el-pagination>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
import { delGoods } from '../../../utils/request'
import { successAlert } from '../../../utils/alert'
export default {
  data(){
    return {
      value:null,//是否需要分页
    }
  },
  computed:{
    ...mapGetters({
      "goodsList":"goods/goodsList",
      "count":"goods/count"
    })
  },
  methods:{
    ...mapActions({
      "requestGoodsList":"goods/goodsListActions",
      "requestCount":"goods/countActions",
      "requestPage":"goods/pageActions",
    }),
    cPage(page){
      this.requestPage(page)
      this.requestGoodsList(this.value)
    },
    edit(id){
      this.$emit('edit',id)
    },
    del(id){
        this.$confirm('确定要删除吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            delGoods({id}).then(res=>{
              successAlert(res.data.msg);
              this.requestCount();
              this.requestGoodsList(null)
            })
      }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
    }
  },
  mounted(){
    this.requestCount()
    this.requestGoodsList(this.value)
  }
}
</script>

<style scoped>
.img{
  width:100px;
}
</style>



